// collection ref
const colRef = collection(db, 'books')

// get collection data

//const q = query(colRef, where("author", "==", "patrick rothfuss"), orderBy('createdAt'))


// realtime collection data
function updateListDiv(array) {
  const listDiv = document.getElementById('listDIv');
  // Assuming your array is an array of objects with title and author properties
  const html = array.map(book => `<p>${book.title} by ${book.author}</p>`).join('');
  listDiv.innerHTML = html;
}

// Function to display the books
function displayBooks() {
  // Assuming colRef is your Firestore collection reference
  onSnapshot(colRef, (snapshot) => {
    let books = [];
    snapshot.docs.forEach(doc => {
      books.push({ ...doc.data(), id: doc.id });
    });
    updateListDiv(books);
  });
}

onSnapshot(colRef, (snapshot) => {
  let books = []
  snapshot.docs.forEach(doc => {
    books.push({ ...doc.data(), id: doc.id })
  })
  console.log(books)
})

// adding docs
const addBookForm = document.querySelector('.add')
addBookForm.addEventListener('submit', (e) => {
  e.preventDefault()

  addDoc(colRef, {
    title: addBookForm.title.value,
    author: addBookForm.author.value,
    createdAt: serverTimestamp()
  })
  .then(() => {
    addBookForm.reset()
  })
})

// deleting docs
const deleteBookForm = document.querySelector('.delete')

deleteBookForm.addEventListener('submit', (e) => {
  e.preventDefault()

  const docRef = doc(db, 'books', deleteBookForm.identificacion.value)

  deleteDoc(docRef)
    .then(() => {
      deleteBookForm.reset()
      
    })
})

